import { Component, NgZone } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';


import 'rxjs/add/operator/takeUntil';
import { Subject } from 'rxjs/Subject';

import { LoopBackConfig } from '../../../shared/sdk';

import { LoginServiceProvider } from '../../providers/login-service/login-service';
import { ConfigServiceProvider } from '../../providers/config-service/config-service';
import { UtilServiceProvider } from '../../providers/util-service/util-service';
import { SharedServiceProvider } from '../../providers/shared-service/shared-service';
import { MessageServiceProvider } from '../../providers/message-service/message-service';

@IonicPage()

@Component({
  selector: 'page-tabs',
  templateUrl: 'tabs.html'
})

export class TabsPage {
  private ngUnsubscribe: Subject<void> = new Subject<void>();
  tab1Root = 'HomePage';
  tab2Root = 'DevicePage';
  tab3Root = 'GpsPage';
  tab4Root = 'PdaPage';
  tab5Root = 'JseaPage';
  tab6Root = 'MsgsPage';
  tab7Root = 'ImagesPage';
  tab8Root = 'BarcodePage';
  username = '';


  //newMessageCount: number = 0; // default 0

  constructor(private nav: NavController, private auth: LoginServiceProvider,
    private configService: ConfigServiceProvider, private utilService: UtilServiceProvider,
    private sharedService: SharedServiceProvider, private messageService: MessageServiceProvider,
    private zone: NgZone) {
    //let info = this.auth.getUserInfo();

    LoopBackConfig.setBaseURL(this.configService.apiURL);
    LoopBackConfig.setApiVersion(this.configService.API_VERSION);
    this.sharedService.messageToDriver.subscribe(payload => {
      this.messageService.getNewMesssageCount();
      this.zone.run(() => {
        console.log('force update the screen');
      });
    });
  }//constuctor

  public logout() {
    this.auth.logout().takeUntil(this.ngUnsubscribe).subscribe(success => {
      this.nav.setRoot('LoginPage');
    });
  }



  // Once the view is done loading (this is triggered automatically)
  ngAfterViewInit() {

    // Get the element by the id 'tab-0-0' attributed by the Ionic team for the first button
    document.getElementById('tab-t0-0').addEventListener('click', event => {

      // Execute your code here when the first button is clicked
      this.sharedService.showSpinner();

    });

    // Get the element by the id 'tab-0-1' attributed by the Ionic team for the second button
    document.getElementById('tab-t0-1').addEventListener('click', event => {

      // Execute your code here when the second button is clicked
      this.sharedService.showSpinner();

    });
    // Get the element by the id 'tab-0-2' attributed by the Ionic team for the third button
    document.getElementById('tab-t0-2').addEventListener('click', event => {

      // Execute your code here when the third button is clicked
      this.sharedService.showSpinner();

    });
    // Get the element by the id 'tab-0-3' attributed by the Ionic team for the fourth button
    document.getElementById('tab-t0-3').addEventListener('click', event => {

      // Execute your code here when the fourth button is clicked
      this.sharedService.showSpinner();

    });
    // Get the element by the id 'tab-0-4' attributed by the Ionic team for the fifth button
    document.getElementById('tab-t0-4').addEventListener('click', event => {

      // Execute your code here when the fifth button is clicked
      this.sharedService.showSpinner();

    });
    // Get the element by the id 'tab-0-5' attributed by the Ionic team for the sixth button
    document.getElementById('tab-t0-5').addEventListener('click', event => {

      // Execute your code here when the sixth button is clicked
      this.sharedService.showSpinner();

    });
    // Get the element by the id 'tab-0-6' attributed by the Ionic team for the seventh button
    document.getElementById('tab-t0-6').addEventListener('click', event => {

      // Execute your code here when the seventh button is clicked
      this.sharedService.showSpinner();

    });

    // Get the element by the id 'tab-0-7' attributed by the Ionic team for the eighth button
    document.getElementById('tab-t0-7').addEventListener('click', event => {

      // Execute your code here when the eighth button is clicked
      this.sharedService.showSpinner();

    });

    
  }



  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}



